/* General Styles */
.App {
  font-family: 'Arial', sans-serif;
  text-align: center;
  padding: 20px;
  background-color: #f5f5f5;
  max-width: 600px;
  margin: 0 auto;
}

select, button {
  padding: 10px 15px;
  margin: 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
  font-size: 16px;
  width: 90%;
  max-width: 300px;
}

button {
  background-color: #4CAF50;
  color: white;
  cursor: pointer;
}

button:hover {
  background-color: #45a049;
}

button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

/* Test Completion Styles */
.TestCompleted {
  background-color: #f0f0f0;
  padding: 10px;
  border-radius: 5px;
  margin: 20px 0;
}

/* Question and Feedback Styles */
.Question, .Feedback {
  background-color: #e3f2fd;
  padding: 15px;
  border-radius: 8px;
  margin: 10px 0;
}

/* Score Display Styles */
.Score, .IncorrectAnswers {
  font-size: 1.2em;
  font-weight: bold;
  color: #1565c0;
}

/* Responsive Styles */
@media (max-width: 600px) {
  .App {
    padding: 10px;
  }

  select, button {
    width: 100%;
  }
}
